import React from 'react';
import { Field, reduxForm } from 'redux-form';

const required = value => value ? undefined : 'Required'

const SimpleForm = props => {
  const { handleSubmit, pristine, reset, submitting } = props;
  return (
    <form onSubmit={handleSubmit}>
      <h1>Prescription Update Api</h1>

      <div className="form-group">
        <label>APIKey</label>
        <div>
          <Field
            name="APIKey"
            component="input"
            className="form-control"
            disabled
          />
        </div>
      </div>

      <div className="form-group">
        <label>PharmacyNumber</label>
        <div>
          <Field
            name="PharmacyNumber"
            component="input"
            className="form-control"
            disabled
          />
        </div>
      </div>

      <div className="form-group">
        <label>transaction_id</label>
        <div>
          <Field
            name="transaction_id"
            component="input"
            className="form-control required"
            validate={[ required ]}
          />
        </div>
      </div>

      <div className="form-group">
        <label>rx_number</label>
        <div>
          <Field
            name="rx_number"
            component="input"
            className="form-control"
          />
        </div>
      </div>

      <div className="form-group">
        <label>patient_id</label>
        <div>
          <Field
            name="patient_id"
            component="input"
            className="form-control"
          />
        </div>
      </div>

      <div className="form-group">
        <label>transaction_date</label>
        <div>
          <Field
            name="transaction_date"
            component="input"
            className="form-control"
          />
        </div>
      </div>

      <div className="form-group">
        <label>transaction_time</label>
        <div>
          <Field
            name="transaction_time"
            component="input"
            className="form-control"
          />
        </div>
      </div>

      <div className="form-group">
        <label>rx_serial_number</label>
        <div>
          <Field
            name="rx_serial_number"
            component="input"
            className="form-control"
          />
        </div>
      </div>

      <div className="form-group">
        <label>drug_name</label>
        <div>
          <Field
            name="drug_name"
            component="input"
            className="form-control"
          />
        </div>
      </div>

      <div className="form-group">
        <label>patient_amount_due</label>
        <div>
          <Field
            name="patient_amount_due"
            component="input"
            className="form-control"
          />
        </div>
      </div>
      
      
      <div className="form-group">
        <button type="submit" class="btn btn-primary" disabled={submitting} >Submit</button>
        {' '}
        <button type="button" class="btn btn-secondary" disabled={pristine || submitting} onClick={reset}>
          Clear Values
        </button>
      </div>
    </form>
  );
};

export default reduxForm({
  initialValues: {
    APIKey: 'APIKey',
    PharmacyNumber: 'PharmacyNumber',
    transaction_id: '888042020191211',
    dob: "1980-01-01",
    transaction_date: (new Date()).toLocaleDateString(),
    transaction_time: (new Date()).toLocaleTimeString(),
    patient_id: (new Date()).getTime(),
    is_active: true,
    gender: 'M',
    rx_number: '12345',
    insurance_plans: [{
      ins_seq_no: 111,
      ins_name: 'Plan',
      ins_code: 'Code'
    }],
    middle_name: 'Middle Name',
    drug_name: 'Advil',
    patient_amount_due: "10",
    rx_serial_number: '1234567890'
  },
  form: 'simple', // a unique identifier for this form
})(SimpleForm);