import React from 'react'
import { Provider } from 'react-redux'
import store from './store'
import SimpleForm from './PrescriptionForm'

var api = 'http://localhost:5000/pills-757d2/us-central1/api';

function submit(data) {
  //console.log('SUBMIT DATA', data)
  var endpoint = 'prescriptions';
  fetch(api + '/bestrxp/' + endpoint, {
    method: 'post',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data)
  });
}

const App = () =>
  <Provider store={store}>
    <div className="container">
    <img 
      className="logo" alt="BestRx"
      src="https://www.bestrx.com/wp-content/uploads/2019/10/logo-small-new.png" 
      width="320" height="106" sizes="320px" 
    />
    <SimpleForm 
        onSubmit={submit} 
    />
    </div>
  </Provider>;

  export default  App;